import Store from '../Store'
import {actions, apis} from '../constants'
import {ajax} from '../../util'

let store = new Store({
  state: {
    list: []
  },
  mutations: {
  },
  actions: {
    [actions.sys.init] (context, param) {
      return new Promise((resolve, reject) => {
        ajax({
          url: apis.sys.init,
          data: param
        }).then(() => {
          resolve()
        }).catch(err => {
          reject(err)
        })
      })
    }
  }
})

export default store
