<template>
  <div class="sys-guide">
    <span class="brand">cleverMock</span>
    <div class="title-panel">
      <div class="title"></div>
    </div>
    <div class="form">
      <form-item label="" :label-width="90">
        <c-input size="big" v-model="form.rootName" :width="300" autoFocus/>
      </form-item>
      <form-item label="" :label-width="90">
        <c-input size="big" v-model="form.rootPassword" :width="300"/>
      </form-item>
      <c-button theme="border" type="primary" size="large" @click="onSave"></c-button>
    </div>
  </div>
</template>

<script>
import FormItem from '../../components/formItem/index'
import {actions} from '../../store/constants'
import {router} from '../../router/constants'
export default {
  name: 'sysGuide',
  components: {FormItem},
  data () {
    return {
      form: {
        rootName: 'admin',
        rootPassword: ''
      }
    }
  },
  methods: {
    onSave () {
      // todo 
      this.$store.dispatch(actions.sys.init, this.form).then(() => {
        this.$router.replace({
          name: router.login
        })
      }).catch(err => {
        this.$message({
          type: 'error',
          message: ''
        })
        console.error('', err)
      })
    }
  }
}
</script>

